.header{
    background: rgb(200, 200, 200);
    display: flex;
    align-items: center;
}
.header .contenedorLogo{
    width: 20%;
    height: 100%;
    text-align: center;
    display: inline-block;
}
.header .contenedorLogo .logo{
    height: 145px;
    padding: 10px;
}
.header .contenedorTitulo{
    width: 80%;
    text-align: center;
    display: inline-block;
}
.header .contenedorTitulo .titulo{
    display: inline-block;
    text-align: center;
    width: 100%;
    font-size: 50px;
}
.aside input[type="radio"]{
    display: none;
}
.aside label{
    color: #000;
    padding: 5px 15px 5px 51px;;
    display: block;
    position: relative;
    cursor: pointer;
}
.aside label:before{
    content: "";
    width: 17px;
    height: 17px;
    display: inline-block;
    background: none;
    border: 3px solid  #000000;
    border-radius: 50%;
    position: absolute;
    left: 17px;
    -webkit-transition: .5s;
    transition: .5s;
}
.aside input[type="radio"]:checked + label:before{
    background: #000000;
}
.sectionHome{
    padding: 5px;
    width: 80%;
    display: inline-block;
}
.footer{
    width: 100%;
    height: 100px;
    padding: 10px;
    background: #464646
}
.footer a{
    text-decoration: none;
    font-size: 40px;
    color: #fff;
    font-weight: bold;
    margin-right: 50px;
}
.footer a:hover{
    text-decoration: underline;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.articleHome{
    width: 100%;
}
.asideHome{
    width: 20%;
    height: 100%;
    display: inline-block;
    float: left;
    background: rgba(0, 96, 117, 0.05);
    padding: 5px;
}
.sectionHome{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.form {
  width: 100%;
  display: inline-block;
}
.form input[type="text"],
.form input[type="number"],
.form .selector,
.form button {
  display: block;
  margin: 10px auto;
  padding: 10px;
  width: 30%;
  background: none;
  border: 1px solid #000;
}
.form button {
  background: rgb(50, 90, 52);
  color: #fff;
  font-size: 30px;
  -webkit-transform: rotate();
          transform: rotate();
}
.form button:hover {
  background: rgb(74, 134, 77);
  -webkit-transition: 1s;
  transition: 1s;
}
.respuesta {
  background: #5bdd4f;
  text-align: center;
  font-size: 20px;
}

